const ajax = new XMLHttpRequest();
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json'; // @id Î∞îÍøîÏπòÌÇ§

const container = document.getElementById('root');
const content = document.createElement('div');
//const ul = document.createElement('ul');

let endFlag;

const store = {
    currentPage : 1, // ÏµúÍ∑º ÌéòÏù¥ÏßÄ

}; 


function getData(url){
    ajax.open('GET', url, false);
    ajax.send();
    return JSON.parse(ajax.response);
}


function newsFeed(){
    const newsFeed = getData(NEWS_URL);
    const newsList = [];
    let template = `
        <div class="bg-gray-600 min-h-screen">
            <div class="bg-white text-xl">
                <div class="mx-auto px-4">
                    <div class="flex justify-between items-center py-6">
                        <div class="flex justify-start">
                            <h1 class="font-extrabold">Hacker News</h1>
                        </div>
                        <div class="items-center justify-end">
                            <a href="#/page/{{__prev_page__}}" class="text-gray-500">
                                Previous
                            </a>
                            <a href="#/page/{{__next_page__}}" class="text-gray-500 ml-4">
                                Next
                            </a>
                        </div>
                    </div>
                </div>
            </div>
            <div class="p4 text-2xl text-gray-700">
                {{__news_feed__}}
            </div>
        </div>
    `; // handlebars templates Ï†ÅÏö©Ìï¥Î≥¥Í∏∞

    for(let i = (store.currentPage - 1) * 10; i < store.currentPage * 10; i++){
        //const div = document.createElement('div');
        

        newsList.push(`
            <li>
                <a href = #/show/${newsFeed[i].id}>
                    ${newsFeed[i].title}(${newsFeed[i].comments_count})
                </a>
            </li>
        `);
    }

    template = template.replace('{{__news_feed__}}', newsList.join('')); //container.innerHTML = newsList.join(''); // Ï°∞Ïù∏ ÏÇ¨Ïö©Ïãú Î∞∞Ïó¥ÏïàÏùò Î¨∏ÏûêÏó¥Îì§ÏùÑ Ìï©Ï≥êÏÑú Î∞òÌôòÌï¥Ï§ÄÎã§, Î∞∞Ïó¥ Ïù∏Îç±Ïä§ ÏÇ¨Ïù¥Ïùò Íµ¨Î∂ÑÏûê(default = ,)Í∞Ä Ï°¥Ïû¨ÌïòÍ∏∞ÎïåÎ¨∏Ïóê ÏóÜÏï†Ï§òÏïºÌïòÎäîÎç∞,, join Ìï®ÏàòÏùò Ï≤´Î≤àÏß∏ ÌååÎùºÎØ∏ÌÑ∞Îäî Íµ¨Î∂ÑÏûêÎ•º Ïñ¥Îñ§Í≤ÉÏùÑ ÏÇ¨Ïö©Ìï†ÏßÄ Ï†ïÌïòÎäî Î∂ÄÎ∂Ñ. Í∑∏Î†áÍ∏∞ÎïåÎ¨∏Ïóê '' ÎùºÎäî Í≥µÎ∞±ÏùÑ ÎÑ£ÏúºÎ©¥ Î¨∏ÏûêÏó¥ÎßåÏùÑ Î∞òÌôòÌï¥Ï§ÄÎã§.
    template = template.replace('{{__prev_page__}}', store.currentPage > 1 ? store.currentPage -1 : 1);
    template = template.replace('{{__next_page__}}', store.currentPage + 1);
    
    container.innerHTML = template;
}


function newsDetail(){

    //console.log(location.hash); //idÍ∞ÄÏ†∏Ïò§Í∏∞ (Î∏åÎùºÏö∞Ï†ÄÍ∞Ä Í∏∞Î≥∏ÏúºÎ°ú Ï†úÍ≥µÌï¥Ï£ºÎäî Í∞ùÏ≤¥)
    const id = location.hash.substr(7);
    const newsContent = getData(CONTENT_URL.replace('@id', id));

    container.innerHTML = `
    <h1>${newsContent.title}</h1>

    <div>
        <a href="#/page/${store.currentPage}">Î™©Î°ùÏúºÎ°ú</a>
    </div>
    `;

    // title.innerHTML = newsContent.title;

    // content.appendChild(title);
    // console.log(newsContent);
    // // ul.innerHTML = `${conTent.content}`;

}

function router(){
    const routePath = location.hash; 

    if(routePath === ''){// location Ìï¥Ïâ¨Ïóê #Ïù¥ Îì§Ïñ¥Ïò§Î©¥ Îπà Í∞í Î∞òÌôòÏù¥ ÎêòÏñ¥ Ï°∞Í±¥Î¨∏Ïù¥ ÎèôÏûëÌïòÎäî Í≤É
        newsFeed();
    } else if (routePath.indexOf('#/page/') >= 0) { // Î¨∏ÏûêÏó¥ Í∞í ÏïàÏóê Ï°¥Ïû¨ÌïòÎ©¥ ÏúÑÏπòÏ†ïÎ≥¥, ÏóÜÏúºÎ©¥ -1
        store.currentPage = Number(routePath.substr(7));
        newsFeed();
    } else {
        newsDetail();
    }
}

window.addEventListener('hashchange', router);

router();

